import React, { PropTypes, Component } from 'react';
import { Header, Form } from 'semantic-ui-react';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';

export default class Step2 extends Component {
  static propTypes = {
    formChange: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { formData, formChange, kycSystem: { kycLabels } } = this.props;
    return (
      <Form>
        <Header
          content="Basic Info"
          subheader="To begin, we'll need some basic info to confirm your identity"
        />
        <Form.Group widths="equal">
          <FormField label="First Name" name="first_name" {...{ formData, formChange }} />
          <FormField label="Last Name" name="last_name" {...{ formData, formChange }} />
        </Form.Group>
        <Form.Group widths="equal">
          <FormField type="date" label="Date of Birth" name="birthday" {...{ formData, formChange }} />
          <FormField type="select" options={kycLabels.genders} label="Gender" name="sex" {...{ formData, formChange }} />
        </Form.Group>
        <Form.Group widths="equal">
          <FormField
            placeholder="e.g. +44 01223 223223"
            label="Phone Number Contact (including country code)"
            name="phone_number"
            {...{ formData, formChange }}
          />
          <FormField
            search
            type="select"
            options={kycLabels.countries}
            label="Nationality"
            name="nationality"
            {...{ formData, formChange }}
          />
        </Form.Group>
      </Form>
    );
  }
}



