import { formatQuery } from 'spectrum-lightsuite/src/helpers/stringUtils';
import { testLoginData, API_URL, PREFIX } from './constants';
import { parseError, keyToActionName, toUpperCase } from './helpers';

function generateActionName(method, type) {
  return `${PREFIX}${keyToActionName(method)}${(type && `_${keyToActionName(type)}`) || ''}`;
}

const resetAction = generateActionName('resetAll');
const authErrorAction = generateActionName('userLogin', 'formError');

function authFetch(endpoint, { loading, success, failure }, objOrFn = {}, done) {
  return (dispatch, getState) => {
    const state = getState();
    const obj = typeof objOrFn === 'function' ? objOrFn(state) : objOrFn;
    const query = obj.query ? formatQuery(obj.query) : '';
    const authToken = (obj.data && obj.data.authToken) || state.kycSystem.authToken;
    if (loading) { dispatch({ type: loading }); }
    const uri = `${API_URL}/${endpoint}${query}`;
    const data = {
      method: obj.method || (obj.data ? 'POST' : 'GET'),
      body: obj.data ? JSON.stringify(obj.data) : undefined,
      headers: {
        Authorization: authToken ? `Bearer ${authToken}` : undefined,
        Accept: 'application/json, text/plain, */*',
        'Content-Type': 'application/json',
        ...obj.headers,
      },
    };
    return fetch(uri, data)
      // parse the response
      .then(res => res.json().then(json => ({ json, res })).catch(() => { throw res.statusText; }))
      // check if response is an error
      .then(({ json, res }) => {
        if (res.status === 200) {
          if (done) {
            return done(dispatch, json, res, getState);
          }
          return dispatch({ type: success, payload: json.data || json });
        }
        const err = parseError(json);
        if (err === 'Not Authenticated') {
          // reset login
          dispatch({ type: resetAction });
          return dispatch({ type: authErrorAction, payload: 'Your session expired. Please login again.' });
        }
        return failure && dispatch({ type: failure, payload: err });
      })
      // catch other errors
      .catch((error) => {
        if (failure) {
          dispatch({ type: failure, payload: parseError(error) });
        } else {
          throw error;
        }
      });
  };
}

const defaultSubReducers = {
  // all
  reset: () => ({}),
  // getters
  fetchError: payload => ({ error: payload, loading: null }),
  fetching: (payload, subState) => ({ ...subState, loading: true, error: null }),
  fetched: payload => ({ ...payload, loading: null, error: null }),
  // forms
  changed: (payload, subState) => ({ ...subState, formData: { ...subState.formData, ...payload } }),
  submitting: (payload, { formData }) => ({ formData, submitting: true }),
  formError: (payload, { formData }) => ({ formData, error: payload }),
  submitted: payload => ({ formData: {}, submitted: payload || true }),
  resetError: (payload, subState) => ({ ...subState, error: null }),
  set: (payload, subState) => (
    payload ? ({ ...subState, ...payload }) : {} // merge or reset with `null`
  ),
};

const defaultActionCreatorGenerators = {
  fetch: ({ fetching, fetched, fetchError }, { endpoint, method }) => params => (
    authFetch(endpoint, { loading: fetching, success: fetched, failure: fetchError }, { method, ...params })
  ),
  submit: ({ submitting, submitted, formError }, { endpoint, method }) => params => (
    authFetch(endpoint, { loading: submitting, success: submitted, failure: formError }, { method, data: params })
  ),
  change: ({ changed }) => (params) => {
    // parse native values
    const payload = params.target ? { [params.target.name]: params.target.value } : params;
    return ({ type: changed, payload });
  },
  reset: ({ reset }) => () => ({ type: reset }),
  resetError: ({ resetError }) => () => ({ type: resetError }),
  set: ({ set }) => payload => dispatch => dispatch({ type: set, payload }),
};

const types = {
  form: {
    subReducers: ['formError', 'reset', 'resetError', 'changed', 'submitting', 'submitted'],
    actionCreators: ['reset', 'change', 'submit', 'resetError'],
  },
  getter: {
    subReducers: ['fetchError', 'reset', 'fetching', 'fetched'],
    actionCreators: ['reset', 'fetch'],
  },
  setter: {
    subReducers: ['set'],
    actionCreators: ['set'],
  },
};

function generateActionNames(key, config) {
  if (!config.type) {
    return { [key]: generateActionName(key) };
  }
  return types[config.type].subReducers.reduce((obj, label) => ({
    ...obj, [label]: generateActionName(key, label),
  }), {});
}

function generateDefaultReducer(actionNameKey, key) {
  return (state, action) => ({
    ...state,
    [key]: defaultSubReducers[actionNameKey](action.payload, state[key]),
  });
}

function generateReducers(key, config, actionNames) {
  return Object.keys(actionNames).reduce((obj, actionNameKey) => {
    const reducerOverride = config.reducer || (((config.override || {}).reducers) || {})[actionNameKey];
    const reducer = reducerOverride || generateDefaultReducer(actionNameKey, key);
    return {
      ...obj,
      [actionNames[actionNameKey]]: reducer,
    };
  }, {});
}

function generateActionCreators(key, config, actionNames) {
  if (!config.type) {
    return { [key]: config.action(actionNames, config) };
  }
  return types[config.type].actionCreators.reduce((obj, actionCreatorKey) => {
    const actionCreatorOverride = (((config.override || {}).actions) || {})[actionCreatorKey];
    const actionCreator = actionCreatorOverride || defaultActionCreatorGenerators[actionCreatorKey];
    return {
      ...obj, [`${key}${toUpperCase(actionCreatorKey)}`]: actionCreator(actionNames, config),
    };
  }, {});
}

export default function generateActionsAndReducer(config) {
  const { defaultState, actionCreators, reducers } = Object.keys(config).reduce((obj, key) => {
    const thisConfig = config[key];
    const actionNames = generateActionNames(key, thisConfig);
    const typeDefaultState = thisConfig.type === 'form' ? { formData: { } } : {};
    return {
      defaultState: { ...obj.defaultState, [key]: thisConfig.defaultState || typeDefaultState },
      actionCreators: { ...obj.actionCreators, ...generateActionCreators(key, thisConfig, actionNames) },
      reducers: { ...obj.reducers, ...generateReducers(key, thisConfig, actionNames) },
    };
  }, {});
  return {
    actionCreators,
    reducer: (state = defaultState, action) => {
      if (!reducers[action.type]) { return state; } // ignore this action
      return reducers[action.type](state, action, defaultState);
    },
  };
}


export const { actionCreators, reducer } = generateActionsAndReducer({
  resetAll: {
    action: ({ resetAll }) => () => ({ type: resetAll }),
    reducer: (state, action, defaultState) => ({ ...state, ...defaultState }),
  },
  userInfo: { type: 'getter', endpoint: 'user' },
  kycLabels: { type: 'getter', endpoint: 'reference_data/show' },
  userLogin: {
    type: 'form',
    endpoint: 'auth_user',
    override: {
      actions: {
        submit: ({ submitting: loading, submitted: success, formError: failure }, { endpoint }) => (data = {}) => {
          if (data.dev) { return { type: success, payload: testLoginData }; } // TODO remove this
          return authFetch(endpoint, { loading, success, failure }, { data });
        },
      },
      reducers: {
        submitted: (state, { payload: { user, auth_token } }) => ({
          ...state, userInfo: user, authToken: auth_token, userLogin: {},
        }),
      },
    },
  },
  retreivePasswordToken: { type: 'getter', endpoint: 'auth/password/edit' },
  userRegistration: { type: 'form', endpoint: 'auth' },
  resetPassword: { type: 'form', endpoint: 'auth/password' },
  changePassword: { type: 'form', endpoint: 'auth/password', method: 'PATCH' },
  kycForm: { type: 'form', endpoint: 'user/kyc' },
  otpEnable: {
    type: 'getter',
    endpoint: 'user/otp',
    method: 'POST',
    override: {
      reducers: {
        reset: state => ({ ...state, otpEnable: {}, otpVerify: {} }),
      },
    },
  },
  otpVerify: { type: 'form', endpoint: 'user/otp', method: 'PATCH' },
  otpDisable: { type: 'form', endpoint: 'user/otp', method: 'DELETE' },
  updateAddress: { type: 'form', endpoint: 'user/authorized_address' },
  usersAdminTableConfig: { type: 'setter', defaultState: { per_page: 10, page: 1 } },
  usersAdminEditItem: { type: 'setter' },
  usersAdminApproveAddress: { type: 'form', endpoint: 'admin/authorized_address', method: 'PATCH' },
  usersAdminApproveOrRejectKyc: {
    type: 'form',
    override: {
      actions: {
        submit: ({ submitting: loading, submitted: success, formError: failure }) => (formData = {}) => {
          if (!formData.rejection_code) { throw new Error('Invalid Code'); }
          const endpoint = `admin/kyc/${formData.id}`;
          const approved = formData.rejection_code === 'approve';
          const method = approved ? 'PATCH' : 'DELETE';
          let data;
          if (approved) {
            if (!formData.identification_number) { throw new Error('ID number not set'); }
            if (!formData.identification_expiration) { throw new Error('ID expiration not set'); }
            data = {
              identification_number: formData.identification_number,
              identification_expiration: formData.identification_expiration,
            };
          } else {
            data = { rejection_code: formData.rejection_code };
          }
          return authFetch(endpoint, { loading, success, failure }, { method, data });
        },
      },
    },
  },
  usersAdmin: {
    type: 'getter',
    override: {
      actions: {
        fetch: ({ fetching, fetched, fetchError }) => (endpoint) => {
          if (!endpoint) { return { type: fetched, payload: { items: null } }; }
          return authFetch(
            endpoint,
            { loading: fetching, success: fetched, failure: fetchError },
            ({ kycSystem: { usersAdminTableConfig: query } }) => ({ query }),
            (dispatch, items, res) => {
              const headers = {
                page: 'X-Page',
                total: 'X-Total',
                perPage: 'X-Per-Page',
              };
              const meta = Object.keys(headers).reduce((o, k) => ({
                ...o,
                [k]: parseInt(res.headers.get(headers[k]), 10),
              }), {});
              return dispatch({ type: fetched, payload: { items, meta } });
            },
          );
        },
      },
    },
  },
});



