import React, { PropTypes, Component } from 'react';
import { Button, Message, Loader, Form } from 'semantic-ui-react';
import { connect } from 'react-redux';

import { parseQuery } from 'spectrum-lightsuite/src/helpers/stringUtils';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import ActiveLink from 'spectrum-lightsuite/src/components/common/active_link';

import { RESET_PASSWORD_REDIRECT_URL } from '../constants';
import { actionCreators } from '../actions';

class ChangePassword extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    submit: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    formChange: PropTypes.func.isRequired,
    getToken: PropTypes.func.isRequired,
  }
  static contextTypes = {
    router: PropTypes.object.isRequired,
  }
  componentDidMount() {
    this.props.reset();
    const { reset_password_token } = parseQuery(this.context.router.route.location.search);
    this.props.getToken({ query: { config: 'default', redirect_url: RESET_PASSWORD_REDIRECT_URL, reset_password_token } });
  }
  render() {
    const {
      submit,
      formChange,
      kycSystem: { retreivePasswordToken, changePassword: { submitting, submitted, error, formData } },
    } = this.props;
    if (retreivePasswordToken.error) { return <ErrorMessage content={retreivePasswordToken.error} />; }
    if (submitting || !retreivePasswordToken.auth_token) { return <Loader active />; }
    if (submitted) {
      return (
        <div>
          <Message
            positive
            header="Your password is reset!"
            content="Please login to continue."
            icon="checkmark"
          />
          <Button
            to="/kyc/login"
            as={ActiveLink}
            content="Back to Login"
            color="green"
            icon="left chevron"
            labelPosition="left"
          />
        </div>
      );
    }
    return (
      <Form onSubmit={() => submit({ ...formData, authToken: retreivePasswordToken.auth_token })}>
        <p>You have reqeusted to change your password; please enter the new password below:</p>
        <FormField type="password" label="Enter New Password" name="password" {...{ formData, formChange }} />
        <FormField type="password" label="Confirm New Password" name="password_confirmation" {...{ formData, formChange }} />
        {error && <ErrorMessage content={error} />}
        <Form.Button type="submit" content="Reset Password" icon="key" color="green" />
      </Form>
    );
  }
}

export default connect(null, {
  reset: actionCreators.changePasswordReset,
  submit: actionCreators.changePasswordSubmit,
  formChange: actionCreators.changePasswordChange,
  getToken: actionCreators.retreivePasswordTokenFetch,
})(ChangePassword);



