import React, { PropTypes, Component } from 'react';
import { Header, Form } from 'semantic-ui-react';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';

export default class Step4 extends Component {
  static propTypes = {
    formChange: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { formData, formChange, kycSystem: { kycLabels } } = this.props;
    return (
      <Form>
        <Header
          content="Earnings"
          subheader="To comply with Anti Money Laundering regulations..."
        />
        <Form.Group widths="equal">
          <FormField
            type="select"
            options={kycLabels.employment_types}
            label="Employment Status"
            name="employment_status"
            {...{ formData, formChange }}
          />
          <FormField
            type="select"
            options={kycLabels.source_of_fund_types}
            label="Source of Funds"
            name="source_of_funds"
            {...{ formData, formChange }}
          />
          <FormField
            type="select"
            options={kycLabels.income_ranges}
            label="Income Range (USD Equivalent)"
            name="income_range"
            {...{ formData, formChange }}
          />
        </Form.Group>
      </Form>
    );
  }
}



