export const BASE_URL =
  (process.env.ENVIRONMENT === 'production' && 'https://digix.global/identity') ||
  (process.env.ENVIRONMENT === 'staging' && 'https://digixdev.com/identity') ||
  'http://localhost:3000';

export const PREFIX = 'DIGIX_KYC_SYSTEM_';
export const API_URL = 'https://kycapi.digixdev.com';
export const NEW_ACCOUNT_REDIRECT_URL = `${BASE_URL}/#/kyc/login?new=1`;
export const RESET_PASSWORD_REDIRECT_URL = `${BASE_URL}/#/kyc/change-password`;



