// TODO fix this imports?
import locationHelperBuilder from 'redux-auth-wrapper/history4/locationHelper';
import { connectedRouterRedirect } from 'redux-auth-wrapper/history4/redirect';

import Loading from './components/loading';


const locationHelper = locationHelperBuilder({});

// TODO dynamic base path

export const kycUserIsAuthenticated = connectedRouterRedirect({
  redirectPath: '/kyc/login',
  authenticatedSelector: ({ kycSystem: { userInfo: { email } } }) => !!email,
  // TODO authenticatingSelector (for localstorage re-join)
  AuthenticatingComponent: Loading,
  wrapperDisplayName: 'KycUserIsAuthenticated',
});

export const kycUserIsNotAuthenticated = connectedRouterRedirect({
  redirectPath: ({ kycSystem: { userInfo: { admin } } }, ownProps) => (
    (admin && '/kyc/admin') || locationHelper.getRedirectQueryParam(ownProps) || '/kyc/status'
  ),
  allowRedirectBack: false,
  // Want to redirect the kycUser when they are done loading and authenticated
  authenticatedSelector: ({ kycSystem: { userInfo: { email }, userLogin: { submitting } } }) => !email && !submitting,
  authenticatingSelector: ({ kycSystem: { userLogin: { submitting } } }) => submitting,
  AuthenticatingComponent: Loading,
  wrapperDisplayName: 'KycUserIsNotAuthenticated',
});

export const kycUserIsAdmin = connectedRouterRedirect({
  redirectPath: '/kyc/',
  allowRedirectBack: false,
  authenticatedSelector: ({ kycSystem: { userInfo: { email, admin } } }) => !!email && admin,
  wrapperDisplayName: 'KycUserIsAdmin',
});



