import React, { PropTypes, Component } from 'react';
import { Header, Form } from 'semantic-ui-react';
import FormImagePreview from 'spectrum-lightsuite/src/components/common/form_image_preview';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';

const tempi18n = {
  en: {
    utility_bill: 'Utility Bill',
    bank_statement: 'Bank Statement',
    passport: 'International Passport',
    ic_card_singapore: 'Singaporean IC Card',
  },
};

export default class Step5 extends Component {
  static propTypes = {
    formChange: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange({ target: { name, value, file } }) {
    this.props.formChange({
      [name]: {
        data: value,
        filename: file.name,
        content_type: 'image/jpeg',
      },
    });
  }
  render() {
    const { formData, formChange, kycSystem: { kycLabels } } = this.props;
    const idProofTypes = kycLabels.identification_proof_types.map(t => ({ id: t.id, name: tempi18n.en[t.name] }));
    const residenceProofTypes = kycLabels.residence_proof_types.map(t => ({ id: t.id, name: tempi18n.en[t.name] }));
    return (
      <Form>
        <Header
          content="Upload Identification Proof"
          subheader={`
            Bla hablah
          `}
        />
        <Form.Group widths="equal">
          <Form.Field>
            <FormField
              type="select"
              label="Residence Proof"
              name="residence_proof_type"
              options={residenceProofTypes}
              {...{ formData, formChange }}
            />
            <br />
            {formData.residence_proof_type &&
              <FormImagePreview
                fluid
                preview={formData.residence_proof && formData.residence_proof.data}
                formChange={this.handleChange}
                resizeWidth={1024}
                name="residence_proof"
                content="Upload Residence Proof"
                icon="upload"
                accept="image/gif, image/jpeg, image/png"
                color="blue"
              />
            }
          </Form.Field>
          <Form.Field>
            <FormField
              type="select"
              label="National ID (Must match provided Nationality)"
              name="identification_type"
              options={idProofTypes}
              {...{ formData, formChange }}
            />
            <br />
            {formData.identification_type &&
              <FormImagePreview
                fluid
                preview={formData.identification_proof && formData.identification_proof.data}
                formChange={this.handleChange}
                resizeWidth={1024}
                name="identification_proof"
                content="Upload Identity Proof"
                icon="upload"
                accept="image/gif, image/jpeg, image/png"
                color="blue"
              />
            }
          </Form.Field>
        </Form.Group>
      </Form>
    );
  }
}



