import React, { PropTypes, Component } from 'react';
import { Form, Divider } from 'semantic-ui-react';

import SuccessHeader from 'spectrum-lightsuite/src/components/common/success_header';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import QrCode from 'spectrum-lightsuite/src/components/common/qr_code';

export default class EnableForm extends Component {
  static propTypes = {
    secret: PropTypes.string,
    loading: PropTypes.bool,
    error: PropTypes.string,
    submitted: PropTypes.bool,
    email: PropTypes.string.isRequired,
    formData: PropTypes.object.isRequired,
    formChange: PropTypes.func.isRequired,
    fetchSecret: PropTypes.func.isRequired,
  }
  static defaultProps = {
    secret: undefined,
    loading: undefined,
    error: undefined,
    submitted: undefined,
  }
  componentDidMount() {
    if (!this.props.secret) {
      this.props.fetchSecret();
    }
  }
  render() {
    const { secret, loading, error, submitted, email, formData = {}, formChange } = this.props;
    if (error) { return null; }
    if (loading) {
      return (
        <div>
          <Divider section clearing hidden />
          <Divider section clearing hidden />
        </div>
      );
    }
    if (submitted) {
      return (
        <SuccessHeader title="Success!">2FA was enabled</SuccessHeader>
      );
    }
    const otpTitle = encodeURIComponent(`Digix KYC - ${email}`);
    const URI = `otpauth://totp/${otpTitle}?secret=${secret}&isuser=DigixIdentity`;
    return (
      <div>
        <QrCode data={URI} />
        <Divider hidden />
        <p>Scan the above QR code into your favourite 2FA app, and enter the generated code below</p>
        <FormField
          type="number"
          name="otp_pin"
          placeholder="Enter Generated 6 digit code"
          size="huge"
          {...{ formData, formChange }}
        />
      </div>
    );
  }
}



