import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Loader } from 'semantic-ui-react';

import MenuSystem from 'spectrum-lightsuite/src/components/common/menu_system';

import PendingKycRequests from './pending_kyc_requests';
import AddressApprovalRequests from './address_approval_requests';
import AddressApproved from './address_approved';
import All from './all';

import { actionCreators } from '../../actions';

const {
  usersAdminTableConfigSet,
  usersAdminFetch,
  usersAdminEditItemSet,
  kycLabelsFetch,
} = actionCreators;

class Admin extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    usersAdminTableConfigSet: PropTypes.func.isRequired,
    usersAdminFetch: PropTypes.func.isRequired,
    usersAdminEditItemSet: PropTypes.func.isRequired,
    kycLabelsFetch: PropTypes.func.isRequired,
  }
  componentDidMount() {
    if (!this.props.kycSystem.kycLabels.countries) {
      this.props.kycLabelsFetch();
    }
  }
  render() {
    const {
      usersAdminTableConfigSet: updateConfig,
      usersAdminFetch: getUsersInfo,
      usersAdminEditItemSet: setEditingItem,
      kycSystem,
      kycSystem: {
        usersAdminTableConfig: tableConfig,
        usersAdminEditItem: editingItem,
        usersAdmin: usersInfo,
        kycLabels,
      },
    } = this.props;
    if (!kycLabels.countries) { return <Loader active />; }
    return (
      <MenuSystem
        equalWidths
        usingRouter
        childProps={{
          kycSystem,
          usersInfo,
          tableConfig,
          kycLabels,
          getUsersInfo,
          editingItem,
          setEditingItem,
          updateConfig: (endpoint, config) => { updateConfig(config); getUsersInfo(endpoint); },
        }}
        parentRoute="/kyc/admin"
        tabs={[
          { name: 'All Users', component: All, path: 'all' },
          { name: 'KYC Requests', component: PendingKycRequests, path: 'pending-kyc' },
          { name: 'Address Change Requests', component: AddressApprovalRequests, path: 'pending-address' },
          { name: 'Approved Addresses', component: AddressApproved, path: 'approved' },
        ]}
      />
    );
  }
}

export default connect(null, {
  usersAdminTableConfigSet,
  usersAdminFetch,
  usersAdminEditItemSet,
  kycLabelsFetch,
})(Admin);



