/* eslint-disable camelcase */
import React, { PropTypes, Component } from 'react';
import { Button, Flag } from 'semantic-ui-react';
import { format } from 'fecha';

import UsersTable from './users_table';
import PendingForm from './pending_form';
import VerificationSubmitButton from './verification_submit_button';

import { getKycField } from '../../helpers';

export default class PendingKycRequests extends Component {
  static propTypes = {
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const { kycLabels } = this.props;
    return (
      <UsersTable
        {...this.props}
        endpoint="admin/kyc/index/pending"
        modalHeader={({ user }) => `Verify User KYC: ${user.first_name} ${user.last_name}`}
        modalContent={props => <PendingForm {...props} />}
        modalActions={props => (
          <div>
            <Button content="Cancel" onClick={props.hide} />
            <VerificationSubmitButton {...props} />
          </div>
        )}
        columns={[
          {
            key: 'created_at',
            name: 'Created',
            renderCell: ({ created_at }) => format(new Date(created_at), 'YYYY-MM-DD HH:mm'),
          },
          {
            key: 'email',
            name: 'Email',
            renderCell: ({ user_email }) => user_email,
          },
          {
            key: 'name',
            name: 'Name',
            renderCell: ({ first_name, last_name }) => `${first_name} ${last_name}`,
          },
          {
            key: 'country',
            name: 'Country',
            renderCell: ({ country }) => getKycField(kycLabels, 'countries', country),
          },
          {
            key: 'income_range',
            name: 'Income',
            renderCell: ({ income_range }) => getKycField(kycLabels, 'income_ranges', income_range),
          },
          {
            key: 'language',
            name: 'language',
            renderCell: ({ language }) => (
              <Flag name={getKycField(kycLabels, 'supported_languages', language) === 'English' ? 'gb' : 'cn'} />
            ),
          },
        ]}
      />
    );
  }
}



