import React, { PropTypes, Component } from 'react';
import MenuSystem from 'spectrum-lightsuite/src/components/common/menu_system';
import digixLogo from 'spectrum-lightsuite/src/assets/digix_logo_minimal_blue_mini.png';
import { Menu, Grid, Divider, Segment } from 'semantic-ui-react';
import { connect } from 'react-redux';

import { kycUserIsAuthenticated, kycUserIsNotAuthenticated, kycUserIsAdmin } from './auth';

import LoginFormComponent from './components/login_form';
import AdminComponent from './components/admin';
import ResetPaswordComponent from './components/reset_password';
import ChangePasswordComponent from './components/change_password';
import StatusComponent from './components/status';
import KycFormComponent from './components/kyc_form';
import RegistrationFormComponent from './components/registration_form';
import LogoutButton from './components/logout_button';

const kycConnect = comp => connect(({ kycSystem }) => ({ kycSystem }))(comp);

// TODO REFACTOR; PASS kycSystem to all
const Login = kycConnect(kycUserIsNotAuthenticated(LoginFormComponent));
const ResetPasword = kycConnect(kycUserIsNotAuthenticated(ResetPaswordComponent));
const ChangePassword = kycConnect(kycUserIsNotAuthenticated(ChangePasswordComponent));
const RegistrationForm = kycConnect(kycUserIsNotAuthenticated(RegistrationFormComponent));
const Status = kycConnect(kycUserIsAuthenticated(StatusComponent));
const KycForm = kycConnect(kycUserIsAuthenticated(KycFormComponent));
const Admin = kycConnect(kycUserIsAuthenticated(kycUserIsAdmin(AdminComponent)));

class KycSystem extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { kycSystem } = this.props;
    const { email, admin } = kycSystem.userInfo;
    return (
      <Grid centered>
        <Grid.Column mobile={16} computer={admin ? 16 : 12}>
          <Menu>
            <Menu.Item header>
              <img src={digixLogo} alt="Digix Global" />
            </Menu.Item>
            <Menu.Item header content="KYC Early Access" />
            {email &&
              <Menu.Menu position="right">
                <Menu.Item header content={email} />
                <Menu.Item>
                  <LogoutButton />
                </Menu.Item>
              </Menu.Menu>
            }
          </Menu>
          <Divider hidden />
          <Segment style={{ minHeight: '15em' }}>
            <MenuSystem
              hidden={!!email}
              equalWidths
              menuProps={{ pointing: true }}
              usingRouter
              parentRoute="/kyc"
              tabs={[
                { name: 'Login', path: 'login', component: Login },
                { name: 'Regsiter', path: 'register', component: RegistrationForm },
                { name: 'Reset Password', path: 'reset-password', component: ResetPasword, hidden: !email },
                { name: 'Change Password', path: 'change-password', component: ChangePassword, hidden: !email },
                { name: 'Status', path: 'status', component: Status, hidden: !email },
                { name: 'Update Details', path: 'update', component: KycForm, hidden: !email },
                { name: 'Admin', path: 'admin', component: Admin, hidden: !email },
              ]}
            />
          </Segment>
        </Grid.Column>
      </Grid>
    );
  }
}

export default kycConnect(KycSystem);



