import React, { PropTypes, Component } from 'react';
import { Flag, Icon, Table } from 'semantic-ui-react';
import { format } from 'fecha';

import { getKycField } from '../../helpers';

import ImageInfo from './image_info';
import VerificationCodeVerifier from './verification_code_verifier';

export default class UserInfoTable extends Component {
  static propTypes = {
    detailed: PropTypes.bool,
    user: PropTypes.object.isRequired,
    kycLabels: PropTypes.object.isRequired,
  }
  static defaultProps = {
    detailed: false,
  }
  constructor(props) {
    super(props);
    this.getField = this.getField.bind(this);
  }
  getField(field, value) {
    return getKycField(this.props.kycLabels, field, value);
  }
  render() {
    const { user, detailed } = this.props;
    const address = user.address || user.authorized_address;
    const email = user.email || user.user_email;
    return (
      <Table definition>
        <Table.Body>
          {detailed && user.approved !== undefined &&
            <Table.Row>
              <Table.Cell>Approved</Table.Cell>
              <Table.Cell>
                <Icon name={user.approved ? 'checkmark' : 'remove'} color={user.approved ? 'green' : 'red'} />
              </Table.Cell>
            </Table.Row>
          }
          {address !== undefined &&
            <Table.Row>
              <Table.Cell>Email</Table.Cell>
              <Table.Cell>{email}</Table.Cell>
            </Table.Row>
          }
          {email !== undefined &&
            <Table.Row>
              <Table.Cell>Address</Table.Cell>
              <Table.Cell>
                <a href={`https://etherscan.io/address/${address}`} target="_blank" rel="noopener">{address}</a>
              </Table.Cell>
            </Table.Row>
          }
          {user.created_at !== undefined &&
            <Table.Row>
              <Table.Cell>Created</Table.Cell>
              <Table.Cell>{format(new Date(user.created_at), 'YYYY-MM-DD HH:mm')}</Table.Cell>
            </Table.Row>
          }
          {user.created_at !== undefined &&
            <Table.Row>
              <Table.Cell>Updated</Table.Cell>
              <Table.Cell>{format(new Date(user.updated_at), 'YYYY-MM-DD HH:mm')}</Table.Cell>
            </Table.Row>
          }
          {user.language !== undefined &&
            <Table.Row>
              <Table.Cell>Language</Table.Cell>
              <Table.Cell><Flag name={user.language === 1 ? 'gb' : 'cn'} /></Table.Cell>
            </Table.Row>
          }
          {user.first_name !== undefined &&
            <Table.Row>
              <Table.Cell>First Name</Table.Cell>
              <Table.Cell>{user.first_name}</Table.Cell>
            </Table.Row>
          }
          {user.last_name !== undefined &&
            <Table.Row>
              <Table.Cell>Last Name</Table.Cell>
              <Table.Cell>{user.last_name}</Table.Cell>
            </Table.Row>
          }
          {user.sex !== undefined &&
            <Table.Row>
              <Table.Cell>Gender</Table.Cell>
              <Table.Cell>{this.getField('genders', user.sex)}</Table.Cell>
            </Table.Row>
          }
          {user.phone_number !== undefined &&
            <Table.Row>
              <Table.Cell>Phone Number</Table.Cell>
              <Table.Cell>{user.phone_number}</Table.Cell>
            </Table.Row>
          }
          {user.birthday !== undefined &&
            <Table.Row>
              <Table.Cell>Brithday</Table.Cell>
              <Table.Cell>{format(new Date(user.birthday), 'YYYY-MM-DD')}</Table.Cell>
            </Table.Row>
          }
          {user.nationality !== undefined &&
            <Table.Row>
              <Table.Cell>Nationality</Table.Cell>
              <Table.Cell>{this.getField('countries', user.nationality)}</Table.Cell>
            </Table.Row>
          }
          {user.address_line_one !== undefined &&
            <Table.Row>
              <Table.Cell>Address Line 1</Table.Cell>
              <Table.Cell>{user.address_line_one}</Table.Cell>
            </Table.Row>
          }
          {user.address_line_two !== undefined &&
            <Table.Row>
              <Table.Cell>Address Line 2</Table.Cell>
              <Table.Cell>{user.address_line_two}</Table.Cell>
            </Table.Row>
          }
          {user.city !== undefined &&
            <Table.Row>
              <Table.Cell>City</Table.Cell>
              <Table.Cell>{user.city}</Table.Cell>
            </Table.Row>
          }
          {user.state !== undefined &&
            <Table.Row>
              <Table.Cell>State</Table.Cell>
              <Table.Cell>{user.state}</Table.Cell>
            </Table.Row>
          }
          {user.country !== undefined &&
            <Table.Row>
              <Table.Cell>Country of Residence</Table.Cell>
              <Table.Cell>{this.getField('countries', user.country)}</Table.Cell>
            </Table.Row>
          }
          {user.postal_code !== undefined &&
            <Table.Row>
              <Table.Cell>Postal Code</Table.Cell>
              <Table.Cell>{user.postal_code}</Table.Cell>
            </Table.Row>
          }
          {user.employment_status !== undefined &&
            <Table.Row>
              <Table.Cell>Employment Status</Table.Cell>
              <Table.Cell>{this.getField('employment_types', user.employment_status)}</Table.Cell>
            </Table.Row>
          }
          {user.source_of_funds !== undefined &&
            <Table.Row>
              <Table.Cell>Source of Funds</Table.Cell>
              <Table.Cell>{this.getField('source_of_fund_types', user.source_of_funds)}</Table.Cell>
            </Table.Row>
          }
          {user.income_range !== undefined &&
            <Table.Row>
              <Table.Cell>Income Range</Table.Cell>
              <Table.Cell>{this.getField('income_ranges', user.income_range)}</Table.Cell>
            </Table.Row>
          }
          {user.identification_pose_url !== undefined &&
            <Table.Row>
              <Table.Cell>Webcam Proof</Table.Cell>
              <Table.Cell>
                <ImageInfo
                  filename={user.identification_pose_file_name}
                  url={user.identification_pose_url}
                  thumb={user.identification_pose_url_thumbnail}
                  size={user.identification_pose_file_size}
                  updated={user.identification_pose_updated_at}
                />
              </Table.Cell>
            </Table.Row>
          }
          {user.verification_code !== undefined &&
            <Table.Row>
              <Table.Cell>Verification Code</Table.Cell>
              <Table.Cell>
                <VerificationCodeVerifier code={user.verification_code} />
              </Table.Cell>
            </Table.Row>
          }
          {user.residence_proof_type !== undefined &&
            <Table.Row>
              <Table.Cell>Proof of Residence Type</Table.Cell>
              <Table.Cell>{this.getField('residence_proof_types', user.residence_proof_type)}</Table.Cell>
            </Table.Row>
          }
          {user.residence_proof_url !== undefined &&
            <Table.Row>
              <Table.Cell>Residence Proof</Table.Cell>
              <Table.Cell>
                <ImageInfo
                  filename={user.residence_proof_file_name}
                  url={user.residence_proof_url}
                  thumb={user.residence_proof_url_thumbnail}
                  size={user.residence_proof_file_size}
                  updated={user.residence_proof_updated_at}
                />
              </Table.Cell>
            </Table.Row>
          }
          {user.identification_type !== undefined &&
            <Table.Row>
              <Table.Cell>ID Type</Table.Cell>
              <Table.Cell>{this.getField('identification_proof_types', user.identification_type)}</Table.Cell>
            </Table.Row>
          }
          {user.identification_proof_url !== undefined &&
            <Table.Row>
              <Table.Cell>ID Proof</Table.Cell>
              <Table.Cell>
                <ImageInfo
                  filename={user.identification_proof_file_name}
                  url={user.identification_proof_url}
                  thumb={user.identification_proof_url_thumbnail}
                  size={user.identification_proof_file_size}
                  updated={user.identification_proof_updated_at}
                />
              </Table.Cell>
            </Table.Row>
          }
        </Table.Body>
      </Table>
    );
  }
}



