import React, { PropTypes, Component } from 'react';

import { Form } from 'semantic-ui-react';

import FormField from 'spectrum-lightsuite/src/components/common/form_field';

import UserInfoTable from './user_info_table';

export default class VerificationForm extends Component {
  static propTypes = {
    formData: PropTypes.object.isRequired,
    formChange: PropTypes.func.isRequired,
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const { formData, formChange, kycLabels } = this.props;
    const options = [{ id: 'approve', name: 'approve' }].concat(kycLabels.kyc_rejection_codes);
    return (
      <div>
        <UserInfoTable {...this.props} />
        <FormField
          type="select"
          name="rejection_code"
          label="Approve / Rejection Reason"
          {...{ options, formData, formChange }}
        />
        {formData.rejection_code && formData.rejection_code === 'approve' &&
          <Form.Group widths="equal">
            <FormField label="ID Number" name="identification_number" {...{ formData, formChange }} />
            <FormField type="date" label="ID Expiration" name="identification_expiration" {...{ formData, formChange }} />
          </Form.Group>
        }
      </div>
    );
  }
}



