import React, { PropTypes, Component } from 'react';
import { Header, Form } from 'semantic-ui-react';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';

export default class Step3 extends Component {
  static propTypes = {
    formChange: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { formData, formChange, kycSystem: { kycLabels } } = this.props;
    const availableContries = kycLabels.countries.filter(({ id }) => kycLabels.blocked_countries.indexOf(id) === -1);
    return (
      <Form>
        <Header
          content="Current Residence"
          subheader={`
            We need to make sure you are currently in a country that we're allowed to server.
            Later on, we'll ask for proof of this address.
          `}
        />
        <FormField
          search
          type="select"
          options={availableContries}
          label="Country of Residence"
          name="country"
          {...{ formData, formChange }}
        />
        {formData.country === undefined ?
          <p>If you cannot find your country of residence, unfortunately we cannot serve you.</p>
          :
          <div>
            <Form.Group widths="equal">
              <FormField label="Unit / Block / House Number" name="address_line_one" {...{ formData, formChange }} />
              <FormField label="Street Name" name="address_line_two" {...{ formData, formChange }} />
            </Form.Group>
            <Form.Group widths="equal">
              <FormField label="City" name="city" {...{ formData, formChange }} />
              <FormField label="State" name="state" {...{ formData, formChange }} />
              <FormField label="Postal Code" name="postal_code" {...{ formData, formChange }} />
            </Form.Group>
          </div>
        }
      </Form>
    );
  }
}



