/* eslint-disable camelcase */
import React, { PropTypes, Component } from 'react';
import { Button, Icon } from 'semantic-ui-react';
import { format } from 'fecha';

import UsersTable from './users_table';
import AllForm from './all_form';
import VerificationSubmitButton from './verification_submit_button';

import { getKycField } from '../../helpers';

export default class All extends Component {
  static propTypes = {
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const {
      kycLabels,
    } = this.props;
    return (
      <UsersTable
        {...this.props}
        endpoint="admin/kyc/index/all"
        modalHeader={({ user }) => `Manage User: ${user.first_name} ${user.last_name}`}
        modalContent={props => <AllForm {...props} />}
        modalActions={props => (
          <div>
            <Button content="Cancel" onClick={props.hide} />
            <VerificationSubmitButton {...props} />
          </div>
        )}
        columns={[
          {
            key: 'approved',
            name: 'Approved',
            renderCell: ({ approved }) => <Icon color={approved ? 'green' : 'red'} name={approved ? 'checkmark' : 'remove'} />,
          },
          {
            key: 'email',
            name: 'Email',
            renderCell: ({ user_email }) => user_email,
          },
          {
            key: 'name',
            name: 'Name',
            renderCell: ({ first_name, last_name }) => `${first_name} ${last_name}`,
          },
          {
            key: 'country',
            name: 'Country',
            renderCell: ({ country }) => getKycField(kycLabels, 'countries', country),
          },
          {
            key: 'address',
            name: 'Address',
            renderCell: ({ authorized_address }) => (
              (!authorized_address && '-') ||
              <a href={`https://etherscan.io/address/${authorized_address}`} target="_blank" rel="noopener">
                {authorized_address.substr(2, 4)}...{authorized_address.substr(-4)}
              </a>
            ),
          },
          {
            key: 'updated_at',
            name: 'Last Updated',
            renderCell: ({ created_at }) => format(new Date(created_at), 'YYYY-MM-DD HH:mm'),
          },
        ]}
      />
    );
  }
}



