import React, { PropTypes, Component } from 'react';
import { Message, Button, Form } from 'semantic-ui-react';
import { connect } from 'react-redux';

import { parseQuery } from 'spectrum-lightsuite/src/helpers/stringUtils';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import ActiveLink from 'spectrum-lightsuite/src/components/common/active_link';

import { actionCreators } from '../actions';

const { userLoginChange, userLoginSubmit } = actionCreators;

class LoginForm extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    userLoginSubmit: PropTypes.func.isRequired,
    userLoginChange: PropTypes.func.isRequired,
  }
  static contextTypes = {
    router: PropTypes.object.isRequired,
  }


  render() {
    const { new: created } = parseQuery(this.context.router.route.location.search);
    const { userLoginSubmit: submit, userLoginChange: formChange, kycSystem: { userLogin: { error, formData } } } = this.props;
    return (
      <Form onSubmit={() => submit(formData)}>
        {created &&
          <Message
            positive
            header="Your account has been created!"
            content="Welcome to the platform. You can now login."
            icon="checkmark"
          />
        }
        <FormField label="Email" name="email" {...{ formData, formChange }} />
        <FormField type="password" label="Password" name="password" {...{ formData, formChange }} />
        {error && <ErrorMessage content={error} />}
        <Button
          basic
          to="/kyc/reset-password"
          as={ActiveLink}
          content="Forgot your Password?"
          size="tiny"
          floated="right"
        />
        <Button type="submit" content="Log In" icon="sign in" color="green" />
      </Form>
    );
  }
}

export default connect(null, { userLoginSubmit, userLoginChange })(LoginForm);



