import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';

import { actionCreators } from '../../actions';

import ConfirmationButton from './confirmation_button';

class AddressApprovalButton extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { kycSystem: { usersAdminApproveAddress: formStatus } } = this.props;
    return (
      <ConfirmationButton
        {...this.props}
        formStatus={formStatus}
        confirmationMessage="The address was accepted"
      />
    );
  }
}

export default connect(null, {
  submit: actionCreators.usersAdminApproveAddressSubmit,
  reset: actionCreators.usersAdminApproveAddressReset,
})(AddressApprovalButton);



