import React, { PropTypes, Component } from 'react';
import { Button } from 'semantic-ui-react';
import EZModal from 'sui-react-ezmodal';
import SuccessHeader from 'spectrum-lightsuite/src/components/common/success_header';

export default class ConfirmationButton extends Component {
  static propTypes = {
    formData: PropTypes.object.isRequired,
    handleComplete: PropTypes.func.isRequired,
    submit: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    formStatus: PropTypes.object.isRequired,
    content: PropTypes.func,
    confirmationMessage: PropTypes.string,
    trigger: PropTypes.node,
  }
  static defaultProps = {
    trigger: undefined,
    content: undefined,
    confirmationMessage: undefined,
  }
  render() {
    const { trigger, content, formData, submit, reset, handleComplete, formStatus, confirmationMessage } = this.props;
    return (
      <EZModal
        size="small"
        loading={formStatus.submitting}
        error={formStatus.error}
        header={!formStatus.submitted ? 'Please Confirm' : 'Success'}
        submitButtonText="Confirm"
        noSubmitButton={!!formStatus.submitted}
        closeButtonText={formStatus.submitted ? 'Done' : 'Cancel'}
        handleSubmit={() => { submit(formData); return false; }}
        onClose={() => {
          if (formStatus.submitted) {
            handleComplete();
          }
          reset();
        }}
        content={content || (() => (
          <div>
            {formStatus.submitted ?
              <SuccessHeader title="Confirmed">
                {confirmationMessage || ''}
              </SuccessHeader>
              :
              <div>
                Are you sure?
              </div>
            }
          </div>
        ))}
        trigger={
          trigger ||
            <Button
              icon="checkmark"
              content="Confirm"
              color="green"
              labelPosition="right"
            />
        }
      />
    );
  }
}



