export function getKycField(kycLabels, field, value) {
  const match = (kycLabels[field] && kycLabels[field].find(({ id }) => id === value));
  return (match && match.name) || '';
}

export function parseError(json = {}) {
  console.log('ERR', json);
  return json.message || (json.errors ? ((json.errors.full_messages || [])[0] || json.errors[0]) : (json.messages || json));
}

export function keyToActionName(str) {
  return str.replace(/([A-Z])/g, s => `_${s.toLowerCase()}`).toUpperCase();
}

export function toUpperCase(str) {
  return `${str[0].toUpperCase()}${str.substr(1)}`;
}



