/* eslint-disable camelcase */
import React, { PropTypes, Component } from 'react';
import { Button, Icon } from 'semantic-ui-react';
import { format } from 'fecha';

import UsersTable from './users_table';
import UserInfoTable from './user_info_table';

import { getKycField } from '../../helpers';

export default class All extends Component {
  static propTypes = {
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const { kycLabels } = this.props;
    return (
      <UsersTable
        {...this.props}
        endpoint="admin/authorized_addresses/approved"
        modalHeader={({ user: { user_information: { email } } }) => `Approved User: ${email}`}
        modalContent={({ user }) => <UserInfoTable {...{ kycLabels }} user={{ ...user, ...user.user_information }} />}
        modalActions={props => (
          <Button content="Cancel" onClick={props.hide} />
        )}
        columns={[
          {
            key: 'name',
            name: 'Name',
            renderCell: ({ user_information: { first_name, last_name } = {} }) => `${first_name} ${last_name}`,
          },
          {
            key: 'email',
            name: 'Email',
            renderCell: ({ user_information: { email } = {} }) => email,
          },
          {
            key: 'country',
            name: 'Country',
            renderCell: ({ user_information: { country } = {} }) => getKycField(kycLabels, 'countries', country),
          },
          {
            key: 'address',
            name: 'Address',
            renderCell: ({ address }) => address,
          },
          {
            key: 'updated_at',
            name: 'Last Updated',
            renderCell: ({ created_at }) => format(new Date(created_at), 'YYYY-MM-DD HH:mm'),
          },
        ]}
      />
    );
  }
}



