import React, { PropTypes, Component } from 'react';

import EnableButton from './enable_button';
import DisableButton from './disable_button';

export default class Otp extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { kycSystem } = this.props;
    if (kycSystem.userInfo.otp_enabled) {
      return (
        <DisableButton {...{ kycSystem }} />
      );
    }
    return (
      <EnableButton {...{ kycSystem }} />
    );
  }
}



