/* eslint-disable camelcase */
import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Button } from 'semantic-ui-react';
import { format } from 'fecha';

import UsersTable from './users_table';

import AddressApprovalInfo from './address_approval_info';

import { getKycField } from '../../helpers';

import AddressApprovalButton from './address_approval_button';

export default class AddressApprovalRequests extends Component {
  static propTypes = {
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const { kycLabels } = this.props;
    return (
      <UsersTable
        {...this.props}
        endpoint="admin/authorized_addresses/approvals"
        modalHeader={({ user: { user_information: { email } } }) => `Address Approval Request: ${email}`}
        modalContent={({ user }) => <AddressApprovalInfo {...{ user, kycLabels }} />}
        modalActions={props => (
          <div>
            <Button content="Cancel" onClick={props.hide} />
            {/*
              TODO, make spectrum transaction
              marketplaceAdmin.approveKyc(account, dgd_milligrams_per_day, expiration_unix_timestamp)
            */}
            <AddressApprovalButton
              {...props}
              formData={{
                address: props.user.address,
                id: props.user.user_id,
                approval_tx: '0x048042d1bb5ba990dab31d689595bc8efaa333a4abba9725c13c069865b020d2',
              }}
            />
          </div>
        )}
        columns={[
          {
            key: 'name',
            name: 'Name',
            renderCell: ({ user_information: { first_name, last_name } = {} }) => `${first_name} ${last_name}`,
          },
          {
            key: 'email',
            name: 'Email',
            renderCell: ({ user_information: { email } = {} }) => email,
          },
          {
            key: 'country',
            name: 'Country',
            renderCell: ({ user_information: { country } = {} }) => getKycField(kycLabels, 'countries', country),
          },
          {
            key: 'address',
            name: 'Address',
            renderCell: ({ address }) => address,
          },
          {
            key: 'updated_at',
            name: 'Last Updated',
            renderCell: ({ updated_at }) => format(new Date(updated_at), 'YYYY-MM-DD HH:mm'),
          },
        ]}
      />
    );
  }
}



