// TODO migrate to confirmtion_button
import React, { PropTypes, Component } from 'react';
import { Label, Button } from 'semantic-ui-react';
import { connect } from 'react-redux';
import SuccessHeader from 'spectrum-lightsuite/src/components/common/success_header';

import { getKycField } from '../../helpers';
import { actionCreators } from '../../actions';

import ConfirmationButton from './confirmation_button';

class VerificationSubmitButton extends Component {
  static propTypes = {
    formData: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    kycLabels: PropTypes.object.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const { user, formData, kycLabels } = this.props;
    const { kycSystem: { usersAdminApproveOrRejectKyc: formStatus } } = this.props;
    if (!formData.rejection_code) { return null; }
    const approve = formData.rejection_code === 'approve';
    return (
      <ConfirmationButton
        {...this.props}
        size="small"
        formStatus={formStatus}
        content={() => (
          <div>
            {formStatus.submitted ?
              <SuccessHeader title="Confirmed">
                The KYC request was <b>{approve ? 'approved' : 'rejected'}</b>.
              </SuccessHeader>
              :
              <div>
                <p>
                  Are you sure you wish to
                  {' '}
                  <Label
                    color={approve ? 'green' : 'red'}
                    content={approve ? 'approve' : 'reject'}
                    as="span"
                  />
                  {' '}
                  {user.first_name} {user.last_name}?
                </p>
                {!approve &&
                  <p>
                    Reason given is <b>{getKycField(kycLabels, 'kyc_rejection_codes', formData.rejection_code)}</b>.
                  </p>
                }
              </div>
            }
          </div>
        )}
        trigger={
          <Button
            icon={approve ? 'checkmark' : 'remove'}
            content={approve ? 'Approve' : 'Reject'}
            color={approve ? 'green' : 'red'}
            labelPosition="right"
          />
        }
      />
    );
  }
}

export default connect(null, {
  submit: actionCreators.usersAdminApproveOrRejectKycSubmit,
  reset: actionCreators.usersAdminApproveOrRejectKycReset,
})(VerificationSubmitButton);



