import React, { PropTypes, Component } from 'react';
import { Message, Loader, Form } from 'semantic-ui-react';
import { connect } from 'react-redux';

import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import { RESET_PASSWORD_REDIRECT_URL } from '../constants';

import { actionCreators } from '../actions';

const { resetPasswordChange, resetPasswordSubmit, resetPasswordReset } = actionCreators;

class ResetPassword extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    resetPasswordSubmit: PropTypes.func.isRequired,
    resetPasswordReset: PropTypes.func.isRequired,
    resetPasswordChange: PropTypes.func.isRequired,
  }
  componentDidMount() {
    this.props.resetPasswordReset();
  }
  render() {
    const {
      resetPasswordSubmit: submit,
      resetPasswordChange: formChange,
      kycSystem: { resetPassword: { submitting, submitted, error, formData } },
    } = this.props;
    if (submitting) { return <Loader active />; }
    if (submitted) {
      return (
        <Message
          positive
          header="Password reset request received!"
          content="Please check your email inbox to continue."
          icon="mail"
        />
      );
    }
    return (
      <Form onSubmit={() => submit({ redirect_url: RESET_PASSWORD_REDIRECT_URL, ...formData })}>
        <FormField label="Email" name="email" {...{ formData, formChange }} />
        {error && <ErrorMessage content={error} />}
        <Form.Button type="submit" content="Reset Password" icon="key" color="green" />
      </Form>
    );
  }
}

export default connect(null, { resetPasswordReset, resetPasswordSubmit, resetPasswordChange })(ResetPassword);



