import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import EZModal from 'sui-react-ezmodal';
import { Message, Button } from 'semantic-ui-react';

import AddressInput from 'spectrum-lightsuite/src/components/common/address_input';
import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import SuccessHeader from 'spectrum-lightsuite/src/components/common/success_header';

import { actionCreators } from '../actions';

const {
  userInfoFetch,
  updateAddressReset,
  updateAddressSubmit,
  updateAddressChange,
} = actionCreators;


class SubmitAddress extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    userInfoFetch: PropTypes.func.isRequired,
    updateAddressReset: PropTypes.func.isRequired,
    updateAddressSubmit: PropTypes.func.isRequired,
    updateAddressChange: PropTypes.func.isRequired,
  }
  render() {
    const {
      userInfoFetch: getInfo,
      updateAddressReset: reset,
      updateAddressSubmit: submit,
      updateAddressChange: formChange,
      kycSystem: { userInfo, updateAddress: { formData, error, submitting, submitted } },
    } = this.props;
    return (
      <EZModal
        {...{ error, loading: submitting, noSubmitButton: !!submitted }}
        header="Update your Marketplace Address"
        trigger={
          <Button
            content={
              (userInfo.address_pending_approval && 'Please wait - we are verifying your request') ||
              'Update Marketplace Address'
            }
            icon="tags"
            color={
              (userInfo.kyc_approved && 'blue') ||
              undefined
            }
            disabled={!userInfo.kyc_approved || userInfo.address_pending_approval}
            labelPosition="right"
          />
        }
        closeButtonText={!submitted ? 'Cancel' : 'Done'}
        onClose={() => {
          reset();
          if (submitted) {
            getInfo();
          }
        }}
        handleSubmit={() => {
          submit(formData);
          return false;
        }}
        content={() => (
          submitted ?
            <SuccessHeader title="Done">Request to update your address was submitted</SuccessHeader>
            :
            <div>
              <Message
                info
                icon="info circle"
                header="Specify an address that purchased DGX can be sent to"
                content="For security reasons, this address will be manually verified by admins."
              />
              <AddressInput
                placeholder="e.g. `0x123...456`"
                label="Marketplace Ethereum Address"
                name="address"
                {...{ formChange, formData }}
              />
              <FormField
                label="2FA Code"
                type="number"
                name="otp_pin"
                placeholder="Enter Generated 6 digit code"
                {...{ formData, formChange }}
              />
            </div>
        )}
      />
    );
  }
}

export default connect(null, {
  userInfoFetch,
  updateAddressReset,
  updateAddressSubmit,
  updateAddressChange,
})(SubmitAddress);



