import React, { PropTypes, Component } from 'react';
import { Label, List } from 'semantic-ui-react';

export default class StatusListItem extends Component {
  static propTypes = {
    header: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    button: PropTypes.node,
    complete: PropTypes.bool,
    pending: PropTypes.bool,
    icon: PropTypes.string,
    info: PropTypes.string,
  }
  static defaultProps = {
    complete: false,
    pending: false,
    icon: undefined,
    button: undefined,
    info: undefined,
  }
  render() {
    const { button, header, complete, pending, icon, info, description } = this.props;
    return (
      <List.Item>
        {button && <div style={{ float: 'right', paddingBottom: '0.3em', paddingLeft: '0.2em' }}>{button}</div>}
        <List.Icon
          name={(complete && 'checkmark') || (pending && 'clock') || icon || 'ellipsis horizontal'}
          color={(complete && 'green') || (pending && 'orange') || undefined}
        />
        <List.Content style={{ width: '100%' }}>
          <List.Header content={header} />
          <List.Description>
            {description}
          </List.Description>
        </List.Content>
        {info && <div style={{ paddingTop: '0.5em' }}><Label color="green" basic content={info} /></div>}
      </List.Item>
    );
  }
}



