import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import EZModal from 'sui-react-ezmodal';
import { Button } from 'semantic-ui-react';

import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import SuccessHeader from 'spectrum-lightsuite/src/components/common/success_header';

import { actionCreators } from '../../actions';

const {
  otpDisableSubmit,
  otpDisableChange,
  otpDisableReset,
  userInfoFetch,
} = actionCreators;

class DisableButton extends Component {
  static propTypes = {
    otpDisableSubmit: PropTypes.func.isRequired,
    otpDisableChange: PropTypes.func.isRequired,
    otpDisableReset: PropTypes.func.isRequired,
    userInfoFetch: PropTypes.func.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  render() {
    const {
      otpDisableReset: reset,
      otpDisableChange: formChange,
      otpDisableSubmit: submit,
      userInfoFetch: updateInfo,
      kycSystem: { otpDisable: { formData, error, submitting, submitted } },
    } = this.props;
    return (
      <EZModal
        size="small"
        submitButtonText="Disable"
        error={error}
        loading={submitting}
        header="Please confirm you wish to disable 2FA"
        trigger={<Button color="red" content="Disable 2FA" icon="unlock" labelPosition="right" />}
        noSubmitButton={!!submitted}
        closeButtonText={submitted ? 'Done' : 'Cancel'}
        onClose={() => {
          reset();
          if (submitted) { updateInfo(); }
        }}
        handleSubmit={() => {
          submit(formData);
          return false;
        }}
        content={() => (
          submitted ?
            <SuccessHeader title="Done">2FA was disabled</SuccessHeader>
            :
            <div>
              <p>Please enter your code to disable 2FA</p>
              <FormField
                type="number"
                name="otp_pin"
                placeholder="Enter Generated 6 digit code"
                {...{ formData, formChange }}
              />
            </div>
        )}
      />
    );
  }
}

export default connect(null, {
  otpDisableSubmit,
  otpDisableChange,
  otpDisableReset,
  userInfoFetch,
})(DisableButton);



