import React, { PropTypes, Component } from 'react';

import { Segment, Image, Grid } from 'semantic-ui-react';

import Webcam from '@digix/react-webcam';

import webcamApproval from '../../assets/webcam_approval.png';

export default class WebcamAccess extends Component {
  static propTypes = {

  }
  render() {
    return (
      <div>
        <p>This part of the process requires permission to use your webcam.</p>
        <Grid divided as={Segment}>
          <Grid.Column width={8}>
            <p>
              <b>
                Please grant permissions to activate your webcam.
                Once you allow access, you may need to refresh.
              </b>
            </p>
            <Image src={webcamApproval} alt="Approve Webcam Instructions" />
          </Grid.Column>
          <Grid.Column width={8}>
            <p>
              <b>Ensure can see the webcam preview:</b>
            </p>
            <Webcam audio={false} width={400} height={300} style={{ width: '100%', background: 'black' }} />
          </Grid.Column>
        </Grid>
        <p>
          Please continue when you can see the preview above.
          If you not have a webcam or are unable to see the preview after granting permission,
          you will have the option of uploading an image instead.
        </p>
      </div>
    );
  }
}



