import React, { PropTypes, Component } from 'react';
import { Label, Image } from 'semantic-ui-react';
import { format } from 'fecha';

import { API_URL } from '../../constants';

export default class ImageInfo extends Component {
  static propTypes = {
    filename: PropTypes.string,
    url: PropTypes.string.isRequired,
    thumb: PropTypes.string.isRequired,
    size: PropTypes.number,
    updated: PropTypes.string,
  }
  static defaultProps = {
    filename: undefined,
    size: undefined,
    updated: undefined,
  }
  render() {
    const { filename, url, thumb, size, updated } = this.props;
    return (
      <div>
        {filename && <Label content={filename} detail={`${size}kb`} />}
        {updated && <span>Updated {format(new Date(updated), 'YYYY-MM-DD')}</span>}
        <br />
        <br />
        <a href={`${API_URL}${url}`} target="_blank" rel="noopener">
          <Image size="medium" src={`${API_URL}${thumb}`} alt="" />
        </a>
      </div>
    );
  }
}



