import React, { PropTypes, Component } from 'react';
import { Divider, Loader, List, Segment, Image, Message, Label, Button, Header, Form } from 'semantic-ui-react';

import Webcam from '@digix/react-webcam';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import Web3Connect from 'spectrum-lightsuite/src/helpers/web3/connect';
import FormImage from 'spectrum-lightsuite/src/components/common/form_image_field';

import WebcamAccess from './webcam_access';

const captureTimeout = 5; // seconds between clicking button and taking photo

const screenshotFormat = 'image/jpeg';
const screenshotFilename = 'webcam.jpg';

class Step6 extends Component {
  static propTypes = {
    formChange: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    web3Redux: PropTypes.object.isRequired,
  }
  constructor(props) {
    super(props);
    this.handleStart = this.handleStart.bind(this);
    this.handleCapture = this.handleCapture.bind(this);
    this.state = { error: false, loading: false, timeout: captureTimeout };
  }
  handleStart() {
    const { web3Redux, formChange } = this.props;
    this.setState({ loading: true });
    web3Redux.web3('eth-mainnet').eth.getBlock('latest').then(({ number, hash }) => {
      if (!number || !hash) { throw new Error('Bad response'); }
      formChange({ verification_code: `${number}-${hash.substr(2, 2)}-${hash.substr(-2, 2)}` });
    }).catch(() => this.setState({ error: true, loading: false }));
  }
  handleCapture() {
    const tick = () => {
      const { timeout } = this.state;
      this.setState({ timeout: timeout - 1 });
      if (timeout) {
        setTimeout(tick, 1000);
      } else {
        const data = this.webcam.getScreenshot();
        this.props.formChange({
          identification_pose: {
            data,
            filename: screenshotFilename,
            content_type: screenshotFormat,
            webcam: true,
          },
        });
        this.setState({ timeout: captureTimeout });
      }
    };
    if (this.state.timeout === captureTimeout) {
      tick();
    }
  }
  renderInstructions() {
    return (
      <div>
        <List bulleted>
          <List.Item>
            You will be asked to write down a verification on a piece of paper. Please have some paper and a pen handy.
          </List.Item>
          <List.Item>
            You will then be asked to show your ID and the verification code on camera.
          </List.Item>
          <List.Item>
            <WebcamAccess />
          </List.Item>
        </List>
        {this.state.error && <ErrorMessage content="Problem fetching verification code, please try again" />}
        {!this.state.loading ?
          <Button
            color="blue"
            content="Read the above? Begin!"
            icon="right chevron"
            labelPosition="right"
            floated="right"
            onClick={this.handleStart}
          />
          :
          <Loader active inline />
        }
        <div style={{ clear: 'both' }} />
      </div>
    );
  }
  renderWebcam() {
    const { formData } = this.props;
    const { timeout } = this.state;
    const showResult = timeout === captureTimeout && formData.identification_pose;
    return (
      <div style={{ textAlign: 'center' }}>
        <Label detail="Verification Code" color="green" size="huge" content={formData.verification_code} />
        <Message
          info
          icon="pencil"
          header={`
            Write the above code on a piece of paper and
            show it to the webcam along with your face and your identification document.
          `}
        />
        <Button
          content={`${!formData.identification_pose ? 'Capture' : 'Try again'} (${timeout} second countdown)`}
          color="red"
          size="large"
          icon="camera"
          disabled={timeout !== captureTimeout}
          onClick={this.handleCapture}
        />
        <br /><br />
        {showResult &&
          <Segment inverted color="blue">
            <p>Looking good? Continue to the next step. If not, take another picture.</p>
            <Image src={formData.identification_pose.data} alt="" />
          </Segment>
        }
        <Segment inverted color={(timeout + 1) % 2 ? 'red' : 'black'} style={{ display: showResult ? 'none' : undefined }}>
          <Webcam
            ref={(webcam) => { this.webcam = webcam; }}
            audio={false}
            screenshotFormat={screenshotFormat}
            style={{ width: '100%' }}
          />
        </Segment>
        <Divider horizontal>or</Divider>
        <FormImage
          fluid
          formChange={
            ({ target: { name, value, file } }) => {
              this.props.formChange({
                [name]: {
                  data: value,
                  filename: file.name,
                  content_type: 'image/jpeg',
                },
              });
            }
          }
          resizeWidth={1024}
          name="identification_pose"
          content="Upload an Image instead of using Webcam"
          icon="upload"
          accept="image/gif, image/jpeg, image/png"
          color="blue"
        />
      </div>
    );
  }
  render() {
    const { formData, web3Redux } = this.props;
    const web3 = web3Redux.web3('eth-mainnet');
    if (!web3) {
      return (
        <ErrorMessage content="You are not connected to Ethereum. Please ensure enable it in the 'networks' tab." />
      );
    }
    return (
      <Form>
        <Header
          content="Proof of Webcam"
          subheader="To make sure your ID isn't stolen, we need an image of you holding your ID along with a verification code"
        />
        {!formData.verification_code ? this.renderInstructions() : this.renderWebcam()}
      </Form>
    );
  }
}


export default Web3Connect(Step6);



