import React, { PropTypes, Component } from 'react';
import { Message } from 'semantic-ui-react';

import Advanced from 'spectrum-lightsuite/src/components/common/advanced';

import UserInfoTable from './user_info_table';


export default class AddressApprovalInfo extends Component {
  static propTypes = {
    user: PropTypes.object.isRequired,
    kycLabels: PropTypes.object.isRequired,
  }
  render() {
    const user = {
      ...this.props.user,
      ...this.props.user.user_information,
    };
    return (
      <div>
        <Message
          info
          icon="id card"
          header={`${user.first_name} ${user.last_name} requested to update their address to:`}
          content={
            <a target="_blank" rel="noopener" href={`https://etherscan.io/address/${user.address}`}>
              {user.address}
            </a>
          }
        />
        <Advanced title="View User Info">
          <UserInfoTable user={user} kycLabels={this.props.kycLabels} />
        </Advanced>
      </div>
    );
  }
}



