import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Button, Divider, Loader } from 'semantic-ui-react';
import MenuSystem from 'spectrum-lightsuite/src/components/common/menu_system';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import ActiveLink from 'spectrum-lightsuite/src/components/common/active_link';

import { actionCreators } from '../../actions';

import Step1 from './step_1';
import Step2 from './step_2';
import Step3 from './step_3';
import Step4 from './step_4';
import Step5 from './step_5';
import Step6 from './step_6';
import Step7 from './step_7';

const steps = [
  {
    component: Step1,
    name: 'Start',
    requiredFields: [],
  },
  {
    component: Step2,
    name: 'Basic Info',
    requiredFields: ['first_name', 'birthday', 'last_name', 'sex', 'phone_number', 'nationality'],
  },
  {
    component: Step3,
    name: 'Residence',
    requiredFields: ['country', 'address_line_one', 'address_line_two', 'city', 'state', 'postal_code'],
  },
  {
    component: Step4,
    name: 'Earnings',
    requiredFields: ['employment_status', 'source_of_funds', 'income_range'],
  },
  {
    component: Step5,
    name: 'ID',
    requiredFields: ['residence_proof_type', 'identification_type', 'residence_proof', 'identification_proof'],
  },
  {
    component: Step6,
    name: 'Proof of Webcam',
    requiredFields: ['identification_pose'],
  },
  {
    component: Step7,
    name: 'Verify & Submit',
  },
];

const {
  kycLabelsFetch,
  kycFormChange,
  kycFormSubmit,
  kycFormResetError,
} = actionCreators;


function sanitizeImageProof(data) {
  return {
    ...data,
    data: ((data || {}).data || '').replace('data:image/jpeg;base64,', ''),
  };
}

function sanitizeFormData(formData) {
  return {
    ...formData,
    residence_proof: sanitizeImageProof(formData.residence_proof),
    identification_proof: sanitizeImageProof(formData.identification_proof),
    identification_pose: sanitizeImageProof(formData.identification_pose),
  };
}

class UpdateForm extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    kycLabelsFetch: PropTypes.func.isRequired,
    kycFormChange: PropTypes.func.isRequired,
    kycFormSubmit: PropTypes.func.isRequired,
    kycFormResetError: PropTypes.func.isRequired,
  }
  static contextTypes = {
    router: PropTypes.object.isRequired,
  }
  constructor(props) {
    super(props);
    this.stepComplete = this.stepComplete.bind(this);
  }
  componentDidMount() {
    if (!this.props.kycSystem.kycLabels.countries) {
      this.props.kycLabelsFetch();
    }
  }
  shouldComponentUpdate() {
    return true;
  }
  stepComplete(n) {
    if (n < 0) { return true; }
    const step = steps[n];
    if (!step) { return false; }
    if (!step.requiredFields) {
      return this.props.kycSystem.kycForm.submitted;
    }
    const { formData } = this.props.kycSystem.kycForm;
    return step.requiredFields.reduce((bool, field) => (
      formData[field] !== undefined && formData[field] !== '' ? bool : false
    ), true);
  }
  renderMenuItems() {
    return steps.map((item, n) => {
      const complete = this.stepComplete(n);
      const disabled = !this.stepComplete(n - 1);
      return {
        path: `step-${n}`,
        name: item.name,
        icon: complete ? 'checkmark' : undefined,
        props: {
          color: complete ? 'green' : undefined,
          active: complete,
          disabled,
        },
        component: item.component,
      };
    });
  }
  render() {
    const {
      kycSystem,
      kycFormChange: formChange,
      kycFormSubmit: submit,
      kycFormResetError: resetError,
      kycSystem: { kycForm, kycForm: { formData = {} }, kycLabels },
    } = this.props;
    if (!kycLabels.countries) { return <Loader active />; }
    if (kycLabels.error) { return <ErrorMessage content="Error fetching form data" />; }
    const path = this.context.router.route.location.pathname;
    const tabIndex = parseInt(path.split('/').pop().split('-').pop(), 0);
    return (
      <MenuSystem
        usingRouter
        equalWidths
        aheadDisabled
        segmented
        menuProps={{ stackable: true }}
        hidden={kycForm.submitted}
        childProps={{
          kycSystem,
          formData,
          resetError,
          formChange,
          stepComplete: this.stepComplete,
          formSubmit: () => submit(sanitizeFormData(formData)),
        }}
        parentRoute="/kyc/update"
        tabs={this.renderMenuItems()}
        renderFooter={() => {
          const complete = this.stepComplete(tabIndex);
          return (
            <div>
              <Divider hidden />
              <Button
                to={((tabIndex > 0 && !kycForm.submitted) && `/kyc/update/step-${tabIndex - 1}`) || '/kyc/status'}
                as={ActiveLink}
                content={((tabIndex > 0 && !kycForm.submitted) && 'Previous Step') || 'Back'}
                floated="left"
                icon="chevron left"
                labelPosition="left"
              />
              {(tabIndex < steps.length - 1 && !kycForm.submitted) &&
                <Button
                  to={`/kyc/update/step-${tabIndex + 1}`}
                  color={complete ? 'green' : undefined}
                  disabled={!complete}
                  as={ActiveLink}
                  content="Next Step"
                  floated="right"
                  icon="chevron right"
                  labelPosition="right"
                />
              }
              <div style={{ clear: 'both' }} />
            </div>
          );
        }}
      />
    );
  }
}

export default connect(null, {
  kycLabelsFetch,
  kycFormChange,
  kycFormSubmit,
  kycFormResetError,
})(UpdateForm);



