import React, { PropTypes, Component } from 'react';
import { Button, Header, Segment, List } from 'semantic-ui-react';
import { connect } from 'react-redux';

import ActiveLink from 'spectrum-lightsuite/src/components/common/active_link';


import { actionCreators } from '../actions';

import Otp from './otp';
import SubmitAddress from './submit_address';
import StatusListItem from './status_list_item';

class Status extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    userInfoFetch: PropTypes.func.isRequired,
  }
  componentDidMount() {
    this.props.userInfoFetch();
  }
  render() {
    const { kycSystem, kycSystem: { userInfo } } = this.props;
    return (
      <div>
        <Header
          content="Registration Process"
          subheader="Completing the steps below will allow you to immediately access the DGX marketplace when it goes live"
          textAlign="center"
        />
        <Segment textAlign="left">
          <List divided relaxed>
            <StatusListItem
              complete
              icon="email"
              header="1. Account Created"
              description={`
                You have created an account with the email ${userInfo.email}.
                We wish you a warm welcome to the platform.
              `}
            />
            <StatusListItem
              complete={userInfo.otp_enabled}
              icon="lock"
              header="2. Two Factor Authentication Enabled"
              description="To secure your account you must enable Two Factor Authentication."
              info={
                userInfo.otp_enabled &&
                  '2FA is enabled.'
              }
              button={
                <Otp {...{ kycSystem }} />
              }
            />
            <StatusListItem
              pending={userInfo.has_pending_kyc}
              complete={userInfo.kyc_approved}
              icon="id card"
              header="3. KYC Form Submitted & Approved"
              description={`
                To comply with industry regulations, we must verify your identity before allowing you to purchase from us.
              `}
              info={
                userInfo.kyc_approved &&
                  'You are currently approved. Re-Submit with a new ID if your old ID is about to expire.'
              }
              button={
                <Button
                  to="/kyc/update"
                  color={
                    ((userInfo.kyc_expired) && 'red') ||
                    ((userInfo.has_pending_kyc || userInfo.kyc_approved) && 'orange') ||
                    (userInfo.otp_enabled && 'green') ||
                    undefined
                  }
                  disabled={!userInfo.otp_enabled || userInfo.has_pending_kyc}
                  as={ActiveLink}
                  content={
                    ((userInfo.kyc_expired) && 'KYC Expired - please Re-Submit') ||
                    (userInfo.kyc_approved && 'Re-Submit KYC Information') ||
                    (userInfo.has_pending_kyc && 'Please wait, we are verifying your submission') ||
                    'Submit KYC Information'
                  }
                  icon="id card"
                  labelPosition="right"
                />
              }
            />
            <StatusListItem
              icon="tags"
              complete={userInfo.authorized_address && !userInfo.address_pending_approval}
              pending={userInfo.address_pending_approval}
              header="4. Marketplace Address Set & Approved"
              button={<SubmitAddress {...{ kycSystem }} />}
              description={`
                One address per user is allowed to receive DGX after purchase.
                You can change this address in the future, but it must be manually verified.
              `}
              info={
                userInfo.authorized_address && `Your current approved address is ${userInfo.authorized_address}.`
              }
            />
            <StatusListItem
              icon="shopping basket"
              header="5. Go Buy some DGX!"
              description="You are now fully approved and ready to purchase DGX tokens! Your will be set a daily limit."
              button={
                <Button
                  disabled
                  content={'Marketplace Coming Soon'}
                  icon="clock"
                  labelPosition="right"
                />
              }
            />
          </List>
        </Segment>
      </div>
    );
  }
}

export default connect(null, { userInfoFetch: actionCreators.userInfoFetch })(Status);



