import React, { PropTypes, Component } from 'react';
import { Label } from 'semantic-ui-react';
import { format } from 'fecha';

import Web3Connect from 'spectrum-lightsuite/src/helpers/web3/connect';

class VerificationCodeVerifier extends Component {
  static propTypes = {
    code: PropTypes.string.isRequired,
    web3Redux: PropTypes.object.isRequired,
  }
  componentDidMount() {
    const blockNumber = this.getBlockNumber();
    if (blockNumber && !this.getBlock()) {
      this.props.web3Redux.web3('eth-mainnet').eth.getBlock(blockNumber);
    }
  }
  getBlock() {
    const web3 = this.props.web3Redux.web3('eth-mainnet');
    const blockNumber = this.getBlockNumber();
    return blockNumber && web3.eth.block(blockNumber);
  }
  getBlockNumber() {
    try {
      return parseInt(this.props.code.split('-')[0], 10);
    } catch (e) {
      return false;
    }
  }
  getState() {
    const blockNumber = this.getBlockNumber();
    if (!blockNumber) {
      return {
        detail: 'Invalid Code',
        color: 'red',
      };
    }
    const block = this.getBlock();
    if (!block) {
      return {
        detail: 'Verifying...',
        color: 'orange',
      };
    }
    const splitCode = this.props.code.split('-');
    if (block.hash.substr(2, 2) === splitCode[1] && block.hash.substr(-2, 2) === splitCode[2]) {
      const timestamp = new Date(block.timestamp * 1000);
      return {
        detail: `Valid ${format(timestamp, 'YY-MM-DD HH:mm')} (Make sure it matches the webcam)`,
        color: 'green',
      };
    }
    return {
      detail: 'Hash is invalid! (Click to check manually)',
      color: 'red',
    };
  }
  render() {
    const block = this.getBlockNumber();
    const link = (block && `https://etherscan.io/block/${block}`) || undefined;
    return (
      <Label
        content={this.props.code}
        href={link}
        as={link ? 'a' : undefined}
        target={link ? '_blank' : undefined}
        rel={link ? 'noopener' : undefined}
        {...this.getState()}
      />
    );
  }
}

export default Web3Connect(VerificationCodeVerifier);



