import React, { PropTypes, Component } from 'react';
import { List, Header } from 'semantic-ui-react';

import WebcamAccess from './webcam_access';

export default class Step1 extends Component {
  static propTypes = {

  }
  render() {
    return (
      <div>
        <Header
          content="Get Verified"
          subheader="Update your KYC information"
        />
        <List bulleted>
          <List.Item>
            The KYC (Know Your Customer) process must be compelted by all individuals wishing to purchase DGX
            using the marketplace.
            If you do not complete this process you will not be able to use the marketplace app
            (but you can still purchase DGX privately such as on secondary exchanges).
          </List.Item>
          <List.Item>
            In this proces you will need to truthfully fill in the form with requested personal information.
          </List.Item>
          <List.Item>
            The data you provide will be cross-checked with an Anti Money Launding database.
          </List.Item>
          <List.Item>
            The approval process is manual will take at least 1 business day.
          </List.Item>
          <List.Item>
            <WebcamAccess />
          </List.Item>
          <List.Item>
            To continue, click &quot;next step&quot;.
          </List.Item>
        </List>
      </div>
    );
  }
}



