import React, { PropTypes, Component } from 'react';
import { Loader, Message, Form } from 'semantic-ui-react';
import { connect } from 'react-redux';

import FormField from 'spectrum-lightsuite/src/components/common/form_field';
import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';

import { NEW_ACCOUNT_REDIRECT_URL } from '../constants';
import { actionCreators } from '../actions';

const { userRegistrationReset, userRegistrationChange, userRegistrationSubmit } = actionCreators;

class LoginForm extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    userRegistrationChange: PropTypes.func.isRequired,
    userRegistrationSubmit: PropTypes.func.isRequired,
    userRegistrationReset: PropTypes.func.isRequired,
  }
  componentDidMount() {
    this.props.userRegistrationReset();
  }
  render() {
    const {
      userRegistrationSubmit: submit,
      userRegistrationChange: formChange,
      kycSystem: { userRegistration: { submitting, submitted, error, formData } },
    } = this.props;
    if (submitting) { return <Loader active />; }
    if (submitted) {
      return (
        <Message
          positive
          header="New user is registered!"
          content={`Please check your email inbox (${submitted.email}) for confirmation.`}
          icon="mail"
        />
      );
    }
    return (
      <Form autocomplete="off" onSubmit={() => submit({ confirm_success_url: NEW_ACCOUNT_REDIRECT_URL, ...formData })}>
        <FormField label="Email" name="email" {...{ formData, formChange }} />
        <FormField type="password" label="Password" name="password" {...{ formData, formChange }} />
        <FormField type="password" label="Password Confirmation" name="password_confirmation" {...{ formData, formChange }} />
        {error && <ErrorMessage content={error} />}
        {<Form.Button type="submit" content="Register New Account" icon="sign in" color="green" />}
      </Form>
    );
  }
}

export default connect(null, { userRegistrationReset, userRegistrationChange, userRegistrationSubmit })(LoginForm);



