import React, { PropTypes, Component } from 'react';
import { Table, Loader } from 'semantic-ui-react';
import EZModal from 'sui-react-ezmodal/src';

import ErrorMessage from 'spectrum-lightsuite/src/components/common/error_message';
import ReduxPaginator from 'spectrum-lightsuite/src/components/common/redux_paginator';

import { getKycField } from '../../helpers';

export default class UserTable extends Component {
  static propTypes = {
    getUsersInfo: PropTypes.func.isRequired,
    updateConfig: PropTypes.func.isRequired,
    usersInfo: PropTypes.object.isRequired,
    setEditingItem: PropTypes.func.isRequired,
    kycLabels: PropTypes.object.isRequired,
    columns: PropTypes.array.isRequired,
    editingItem: PropTypes.object.isRequired,
    endpoint: PropTypes.string.isRequired,
    modalHeader: PropTypes.func.isRequired,
    modalContent: PropTypes.func.isRequired,
    modalActions: PropTypes.func.isRequired,
    kycSystem: PropTypes.object.isRequired,
  }
  constructor(props) {
    super(props);
    this.handleComplete = this.handleComplete.bind(this);
  }
  componentDidMount() {
    this.props.getUsersInfo(null);
  }
  getkycLabels(field, value) {
    return getKycField(this.props.kycLabels, field, value);
  }
  handleComplete() {
    this.props.setEditingItem(null);
    this.props.getUsersInfo(this.props.endpoint); // refresh the page
  }
  renderModal() {
    const {
      handleComplete,
      props: {
        editingItem: user,
        kycLabels,
        setEditingItem,
        modalHeader,
        modalContent,
        modalActions,
        kycSystem,
      },
    } = this;
    if (!user.id) { return null; }
    return (
      <EZModal
        initiallyOpen
        data={{ id: user.id }}
        handleSubmit={() => false} // don't close when user hits enter
        onClose={() => setEditingItem(null)}
        header={modalHeader({ kycLabels, user })}
        content={props => modalContent({ ...props, kycLabels, kycSystem, user })}
        actions={props => modalActions({ ...props, kycLabels, user, handleComplete, kycSystem })}
      />
    );
  }
  render() {
    return (
      <div>
        {this.renderModal()}
        <ReduxPaginator
          id={this.props.endpoint}
          renderError={error => <ErrorMessage content={error} />}
          renderLoading={() => <Loader active />}
          renderMissing={() => (
            <Table.Row><Table.Cell warning colSpan="100" textAlign="center">No Results</Table.Cell></Table.Row>
          )}
          actions={{
            fetchData: (...params) => this.props.getUsersInfo(this.props.endpoint, ...params),
            updateConfig: (...params) => this.props.updateConfig(this.props.endpoint, ...params),
          }}
          onRowClick={this.props.setEditingItem}
          data={this.props.usersInfo}
          columns={this.props.columns}
        />
      </div>
    );
  }
}



