import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import EZModal from 'sui-react-ezmodal';
import { Button } from 'semantic-ui-react';

import EnableForm from './enable_form';

import { actionCreators } from '../../actions';

const {
  userInfoFetch,
  otpEnableFetch,
  otpEnableReset,
  otpVerifySubmit,
  otpVerifyChange,
} = actionCreators;

class EnableButton extends Component {
  static propTypes = {
    kycSystem: PropTypes.object.isRequired,
    userInfoFetch: PropTypes.func.isRequired,
    otpVerifySubmit: PropTypes.func.isRequired,
    otpEnableFetch: PropTypes.func.isRequired,
    otpEnableReset: PropTypes.func.isRequired,
    otpVerifyChange: PropTypes.func.isRequired,
  }
  render() {
    const {
      userInfoFetch: getInfo,
      otpVerifySubmit: submit,
      otpVerifyChange: formChange,
      otpEnableFetch: fetchSecret,
      otpEnableReset: reset,
    } = this.props;
    const { kycSystem: { userInfo, otpEnable, otpVerify } } = this.props;
    const submitted = !!otpVerify.submitted;
    return (
      <EZModal
        size="small"
        header="Enable 2FA"
        error={otpVerify.error || otpEnable.error}
        loading={otpVerify.submitting || otpEnable.loading}
        content={() => (
          <EnableForm
            {...{
              formChange,
              fetchSecret,
              submitted,
              loading: otpEnable.loading,
              error: otpEnable.error,
              secret: otpEnable.otp_secret,
              email: userInfo.email,
              formData: otpVerify.formData,
            }}
          />
        )}
        closeButtonText={submitted ? 'Done' : 'Cancel'}
        noSubmitButton={!!submitted}
        trigger={<Button color="green" content="Enable 2FA" icon="lock" labelPosition="left" />}
        onClose={() => {
          reset();
          if (submitted) { getInfo(); }
        }}
        handleSubmit={() => {
          submit(otpVerify.formData);
          return false;
        }}
      />
    );
  }
}

export default connect(null, {
  userInfoFetch,
  otpEnableFetch,
  otpEnableReset,
  otpVerifySubmit,
  otpVerifyChange,
})(EnableButton);



